"use client";

import { motion } from "framer-motion";
import MagneticButton from "./ui/MagneticButton";

export default function Hero() {
  const words = ["WE", "BUILD", "THE", "INFRASTRUCTURE", "THAT", "POWERS", "TOMORROW'S", "ENTERPRISES"];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.08,
        delayChildren: 0.3,
      },
    },
  };

  const wordVariants = {
    hidden: {
      opacity: 0,
      y: 40,
      rotateX: -45,
      filter: "blur(10px)",
    },
    visible: {
      opacity: 1,
      y: 0,
      rotateX: 0,
      filter: "blur(0px)",
      transition: {
        type: "spring",
        stiffness: 100,
        damping: 15,
      },
    },
  };

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">

      {/* Animated Blobs */}
      <div className="blob blob-cyan absolute top-[10%] left-[10%]" />
      <div className="blob blob-pink absolute bottom-[20%] right-[10%]" />
      <div className="blob blob-purple absolute top-[50%] left-[30%]" />

      {/* Content */}
      <div className="relative z-10 text-center px-6 max-w-6xl">

        {/* Headline */}
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          className="text-5xl md:text-7xl lg:text-8xl font-display leading-[1.1] mb-8"
        >
          {words.map((word, i) => (
            <motion.span
              key={i}
              variants={wordVariants}
              className={`inline-block mr-3 md:mr-5 ${word === "TOMORROW'S" ? "gradient-text" : "text-text-primary"
                }`}
              style={{ perspective: "1000px" }}
            >
              {word}
            </motion.span>
          ))}
        </motion.div>

        {/* Tagline */}
        <motion.p
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1, duration: 0.6 }}
          className="text-lg md:text-xl text-text-secondary mb-12 font-accent tracking-wider"
        >
          AI Agents • Custom CRM • Intelligent Systems
        </motion.p>

        {/* CTA */}
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 1.2, type: "spring", stiffness: 200 }}
        >
          <MagneticButton className="px-10 py-5 bg-gradient-to-r from-electric-cyan to-laser-purple rounded-full text-lg font-bold text-bg-primary shadow-2xl glow-cyan">
            SEE OUR WORK ↓
          </MagneticButton>
        </motion.div>

      </div>

      {/* Scroll Indicator */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 2 }}
        className="absolute bottom-12 left-1/2 -translate-x-1/2"
      >
        <motion.div
          animate={{ y: [0, 10, 0] }}
          transition={{ duration: 1.5, repeat: Infinity }}
          className="w-6 h-10 border-2 border-text-muted rounded-full flex justify-center pt-2"
        >
          <div className="w-1.5 h-3 bg-electric-cyan rounded-full" />
        </motion.div>
      </motion.div>

    </section>
  );
}
